
.karnaugh-page {
    display: grid;
    grid-template-rows: min-content minmax(0, 1fr);

    height: 100%;
    width: 100%;

    box-sizing: border-box;
    padding: var(--spacing-l);
}

.karnaugh-page header {
    display: grid;
    grid-template-columns: auto auto;

    box-sizing: border-box;
    padding-bottom: 8rem;
}

.karnaugh-page header > div:nth-child(2) {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    flex-direction: row;

    width: 50vw;
}

.karnaugh-page main {
    display: grid;
    grid-template-rows: minmax(0, 1fr) min-content;
}

.karnaugh-page main > .main-content {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-content: space-around;
}

.karnaugh-page main > .main-content ul.rect-list {
    height: 40rem;
    min-width: 15rem;

    background-color: var(--bg0);
    padding: 1rem;
    border-radius: 1em;
}

.karnaugh-page main > .main-content > div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.karnaugh-page main > .main-content > div > h2 {
    margin-bottom: 8rem;
}

.karnaugh-page main > div:nth-child(2) {
    display: grid;
    place-items: center;
}

.expression {
    padding: 1.5rem 6rem;
    background-color: var(--bg0);
    border-radius: 1em;
}

.expression > h2 {
    margin: 0;
}

.highlighted {
    color: var(--p0);
}